
"use client";

import { useState, useMemo } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { CreateInvoiceDialog } from "@/components/create-invoice-dialog";
import { useToast } from "@/hooks/use-toast";
import { initialInvoices, Invoice } from "@/lib/constants";
import { FinancialOverviewChart } from "@/components/charts/financial-overview-chart";
import { PlusCircle, Download, MoreHorizontal, Calendar as CalendarIcon } from "lucide-react";
import { format, getYear, getMonth } from "date-fns";

export default function Page() {
    const [invoices, setInvoices] = useState<Invoice[]>(initialInvoices);
    const [isDialogOpen, setDialogOpen] = useState(false);
    const { toast } = useToast();

    const [filterDate, setFilterDate] = useState<Date | undefined>(undefined);
    const [filterMonth, setFilterMonth] = useState<string>("all");
    const [filterYear, setFilterYear] = useState<string>("all");
    
    const years = useMemo(() => {
        const allYears = invoices.map(inv => getYear(new Date(inv.dueDate)));
        return [...new Set(allYears)].sort((a, b) => b - a).map(String);
    }, [invoices]);

    const handleInvoiceCreated = (newInvoice: Omit<Invoice, 'id'>) => {
        const invoiceToAdd: Invoice = {
            ...newInvoice,
            id: `INV-${Date.now()}`
        };
        const updatedInvoices = [invoiceToAdd, ...invoices];
        setInvoices(updatedInvoices);
        initialInvoices.unshift(invoiceToAdd); // Persist for mock
        toast({
            title: "Invoice Created",
            description: `Invoice for ${newInvoice.patientName} has been successfully created.`
        });
        setDialogOpen(false);
    };

    const handleExport = () => {
        toast({
            title: "Exporting Data",
            description: "Your billing data is being prepared for download."
        });
    }
    
    const filteredInvoices = useMemo(() => {
        return invoices.filter(invoice => {
            const invoiceDate = new Date(invoice.dueDate);
            if (filterDate && format(invoiceDate, 'yyyy-MM-dd') !== format(filterDate, 'yyyy-MM-dd')) {
                return false;
            }
            if (filterMonth !== 'all' && getMonth(invoiceDate) !== parseInt(filterMonth)) {
                return false;
            }
            if (filterYear !== 'all' && getYear(invoiceDate) !== parseInt(filterYear)) {
                return false;
            }
            return true;
        });
    }, [invoices, filterDate, filterMonth, filterYear]);


    return (
        <>
            <div className="space-y-6">
                <Card>
                    <CardHeader>
                        <CardTitle>Billing Dashboard</CardTitle>
                        <CardDescription>Overview of all financial activities.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <FinancialOverviewChart />
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader>
                        <div className="flex justify-between items-center">
                            <div>
                                <CardTitle>All Invoices</CardTitle>
                                <CardDescription>Create, view, and manage all patient invoices.</CardDescription>
                            </div>
                            <div className="flex items-center gap-2">
                                 <Popover>
                                    <PopoverTrigger asChild>
                                      <Button
                                        variant={"outline"}
                                        className="w-[200px] justify-start text-left font-normal"
                                      >
                                        <CalendarIcon className="mr-2 h-4 w-4" />
                                        {filterDate ? format(filterDate, "PPP") : <span>Filter by date</span>}
                                      </Button>
                                    </PopoverTrigger>
                                    <PopoverContent className="w-auto p-0">
                                      <Calendar
                                        mode="single"
                                        selected={filterDate}
                                        onSelect={setFilterDate}
                                        initialFocus
                                      />
                                    </PopoverContent>
                                 </Popover>
                                 <Select value={filterMonth} onValueChange={setFilterMonth}>
                                     <SelectTrigger className="w-[150px]">
                                         <SelectValue placeholder="Filter by month" />
                                     </SelectTrigger>
                                     <SelectContent>
                                         <SelectItem value="all">All Months</SelectItem>
                                         {Array.from({length: 12}).map((_, i) => (
                                             <SelectItem key={i} value={String(i)}>{format(new Date(0, i), 'MMMM')}</SelectItem>
                                         ))}
                                     </SelectContent>
                                 </Select>
                                 <Select value={filterYear} onValueChange={setFilterYear}>
                                     <SelectTrigger className="w-[120px]">
                                         <SelectValue placeholder="Filter by year" />
                                     </SelectTrigger>
                                     <SelectContent>
                                         <SelectItem value="all">All Years</SelectItem>
                                         {years.map(year => (
                                             <SelectItem key={year} value={year}>{year}</SelectItem>
                                         ))}
                                     </SelectContent>
                                 </Select>
                                <Button onClick={handleExport} variant="outline">
                                    <Download className="mr-2 h-4 w-4" />
                                    Export
                                </Button>
                                <Button onClick={() => setDialogOpen(true)}>
                                    <PlusCircle className="mr-2 h-4 w-4" />
                                    Create Invoice
                                </Button>
                            </div>
                        </div>
                    </CardHeader>
                    <CardContent>
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Invoice ID</TableHead>
                                    <TableHead>Patient</TableHead>
                                    <TableHead>Amount</TableHead>
                                    <TableHead>Due Date</TableHead>
                                    <TableHead>Status</TableHead>
                                    <TableHead><span className="sr-only">Actions</span></TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {filteredInvoices.map((invoice) => (
                                    <TableRow key={invoice.id}>
                                        <TableCell className="font-medium">{invoice.id}</TableCell>
                                        <TableCell>{invoice.patientName}</TableCell>
                                        <TableCell>${invoice.amount.toFixed(2)}</TableCell>
                                        <TableCell>{invoice.dueDate}</TableCell>
                                        <TableCell>
                                            <Badge variant={
                                                invoice.status === 'Pending' ? 'secondary' : invoice.status === 'Overdue' ? 'destructive' : 'default'
                                            } className={invoice.status === 'Paid' ? 'bg-green-500' : ''}>{invoice.status}</Badge>
                                        </TableCell>
                                        <TableCell className="text-right">
                                            <DropdownMenu>
                                                <DropdownMenuTrigger asChild>
                                                    <Button variant="ghost" className="h-8 w-8 p-0">
                                                        <span className="sr-only">Open menu</span>
                                                        <MoreHorizontal className="h-4 w-4" />
                                                    </Button>
                                                </DropdownMenuTrigger>
                                                <DropdownMenuContent align="end">
                                                    <DropdownMenuItem>View Details</DropdownMenuItem>
                                                    <DropdownMenuItem>Mark as Paid</DropdownMenuItem>
                                                    <DropdownMenuItem className="text-destructive">Delete</DropdownMenuItem>
                                                </DropdownMenuContent>
                                            </DropdownMenu>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>
            </div>
            <CreateInvoiceDialog
                isOpen={isDialogOpen}
                onClose={() => setDialogOpen(false)}
                onInvoiceCreated={handleInvoiceCreated}
            />
        </>
    )
}
